.autocomplete {
  position: relative;
  .searchBox {
    position: absolute;
    top: 33px;
    background: white;
    border: 1px solid #ccc;
    border-radius: 4px;
    width: 94%;
    padding: 8px;
  }
  .loader {
    position: absolute;
    right: 26px;
    top: 4px;
    border: 3px solid #f3f3f3; /* Light grey */
    border-top: 3px solid #3498db; /* Blue */
    border-radius: 50%;
    width: 25px;
    height: 25px;
    animation: spin 2s linear infinite;
  }
  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
}
